
    module.exports = new Promise(function (resolve, reject) {
      try {
        /* lazy load group of files */
        require.ensure([], function (require) {
          var requireContext = require.context('!!front-matter-loader!data/works', true, /^(?!.*.draft.es.md$).*.es.md$/i);
          var modules = requireContext.keys().map(function (fileFullName) {
            return requireContext(fileFullName);
          });

          resolve(modules);
        }, 'data.works')
      } catch (error) {
        reject(error);
      }
    });

    if (module.hot && true) {
      module.hot.dispose(data => {
        data.updated = true;
      });
      if (module.hot.data && module.hot.data.updated) {
        var event = new CustomEvent( 'lazyDirLoaderFilesChange' );
        document.dispatchEvent(event);
      }
      module.hot.accept();
    }
  